<template>
    <div>
        <transition-group tag="ul" name="slide" class="users-list" :class="{ isSmall: isSmall }">
            <li v-for="user in users" :key="user.username" class="user">
                <img :src="user.imageMedium" :alt="user.name.first + ' ' + user.name.last" class="user__img">
                
                <transition name="fade">
                    <div class="user__content" :class="{ isHidden: isHidden }">
                        <p>
                            <span class="user__name">
                                <slot name="title" :use="user">
                                    {{user.name.first}} {{user.name.last}}
                                </slot>
                            </span>
                        </p>
                        <slot name="user__subtitle" :user="user">
                            <p class="description">
                                <span class="fa fa-user description__icon description__item"></span>
                                <span class="description__text description__item">{{user.username}}</span>
                            </p>
                        </slot>
                        <p class="description">
                            <span class="fa fa-map-marker loc description__icon description__item"></span>
                            <span class="description__text description__item">{{user.state}}</span>
                        </p>

                        <router-link :to="`/user/${user.username}`" class="button-line">
                            <span class="button-line__icon fas fa-info"></span>
                            <span>more info</span>
                        </router-link>
                    </div>
                </transition>
                
                <span class="icon icon-close fas fa-times-circle" @click="userRemoveEmit(user)"></span>
            </li>
        </transition-group>
    </div>
</template>



<script>
export default {
    props: ['users', 'isHidden', 'isSmall'],
    methods: {
        userRemoveEmit(userToRemove) {
            this.$emit("remove", userToRemove);
        }
    },
}
</script>



<style lang="scss" scoped>

.users-list {
    max-width: 550px;
    margin: 2em auto;
    padding: 0;

    &.isSmall {
        max-width: 700px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        transition: all 0.5s ease-out 0.8s;

        .user {
            width: 30%;
            margin: 2%;
            transition: all 0.5s ease-out 0.5s;
        }
    }
}

.user {
    width: calc(100% - 2rem);
    margin: 1rem;
    padding: 20px;
    display: flex;
    position: relative;
    list-style: none;
    background-color: var(--color-brand-2);


    &__content {
        margin-left: 1rem;
        display: inline-block;
        color: #fff;

        &.isHidden{
            opacity: 0;
            display: none;
            transition: all 0.5s ease-out 0s;
        }
    }

    &__name{
        margin-bottom: .25rem;
        text-transform: capitalize;
        font-size: 1.6rem;
        font-weight: 200;
    }

    &__img {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 3px solid #fff;
    }
}


.description {
    display: flex;
    align-items: center;

    &__icon {
        margin-right: .25rem;
        padding: 0.1rem 0.2rem;
        font-size: 1rem;
        opacity: .75;
    }
    &__text {
        font-size: 1.2rem;
    }
}

.button-line {
    margin-top: 10px;
    padding: 10px 20px;
    display: inline-block;
    text-align: center;
    font-size: 1.2rem;
    color: var(--color-brand-1);
    font-weight: 700;
    border: 2px solid var(--color-brand-1);
    cursor: pointer;

    &__icon {
        margin-right: 1rem;
    }

    &:hover{
        color: #fff;
        border-color: #fff;
    }
}


.icon {
    display: inline-block;
    font-size: 1.8rem;
    color: var(--color-brand-1);

    &:hover {
        color: #fff;
    }
}


.icon-close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}



/* 
VUE TRANSITIONS
-----------------------------------------
*/


/* animation title (WITH NAME FADE) */
.fade-enter {
    opacity: 1;
}

.fade-enter-active {
    transition: all 1s ease-in-out 0s;
}

.fade-leave-to {
    opacity: 0;
}

.fade-leave-active {
    transition: all 0.5s ease-out 0s;
}


/* animation list (WITH THE NAME SLIDE) */
.slide-enter {
    opacity: 1;
    transform: translateX(500px);
}

.slide-enter-active {
    transition: all 1s ease-in-out 0s !important;
}

.slide-leave-to {
    opacity: 0;
    transform: translateX(-500px);
}

.slide-leave-active {
    transition: all 1s ease-in-out 0s !important;
}


/* animation button random (WITH THE NAME SLIDE)*/
.slide-move {
    transition: all 0.5s ease-out 0s !important;
}
</style>
